class Piece extends PureComponent {
	static pieceStyleMap = {
    'pawn': PiecePawn,
    'king': PieceKing,
    'circle': PieceCircle,
    'certificate': PieceCertificate,
    'sun': PieceSun,
    'rocket': PieceRocket,
    'bug': PieceBug,
    'eye': PieceEye,
    'user': PieceUser,
    'halloween-witch-hat': PieceHalloweenWitchHat,
    'halloween-cauldron': PieceHalloweenCauldron,
    'halloween-bat': PieceHalloweenBat,
    'halloween-spider': PieceHalloweenSpider,
    'halloween-cat': PieceHalloweenCat,
    'halloween-tombstone': PieceHalloweenTombstone,
    'halloween-pumpkin': PieceHalloweenPumpkin,
    'halloween-ghost': PieceHalloweenGhost,
  };

	render() {
  	const {style, colour, rotated} = this.props;
    const PieceStyle = this.constructor.pieceStyleMap[style];
  	return (
    	<PieceStyle colour={colour} rotated={rotated} />
    );
  }
}

Piece.propTypes = {
  style: PropTypes.oneOf(Object.keys(Piece.pieceStyleMap)).isRequired,
  colour: PropTypes.oneOf(['white', 'black']).isRequired,
  rotated: PropTypes.bool.isRequired,
};

class PieceDefinitions extends PureComponent {
	render() {
  	return Object.values(Piece.pieceStyleMap)
    	.map(PieceStyle => <PieceStyle.Definition key={PieceStyle.displayName || PieceStyle.name} />);
  }
}
Piece.Definitions = PieceDefinitions;

export default Piece;
